	module.exports = {"data":{"markdownRemark":{"html":"<p>ヤン（Yang）大統領は、復旦大学の優秀な教授制度と私を候補者として指名する準備が非常に触れられており、</p>\n<p>復旦大学は私に大きな名誉を与えたいと思っています。私の心はとても不安です。一方で、私は幸せです。復旦への貧弱な努力は認められていますが、反面、復旦にはあまりにも少なかったので、私たちは不安です。復旦が私たちに与えたすべてのものと比較して、私の努力は適切であるだけでなく、十分でもありません。復旦大学の発展に多くの熱意と追求を寄与し、教師の神聖な任務に輝きを与えることが、私の唯一の適切な答えであると私は思っています。学生は真剣に奉仕しています。</p>\n<p>「復旦は私の魔法のような名前ですが、どこに行っても深く恋しいです。私の成熟した人生は主に復旦で過ごしました。復旦は私を育てただけでなく、私は他のどこにも行くことができない何か、私は深い「復旦複合体」を持っています。</p>\n<p>「復旦人として、最も必要なのは、信頼、理解、支持、懸念だ」と考えている。若者にすべての人々をケアする機会を与えることは、復旦市全体の基本方針であるべきだ。福民は、そのような雰囲気を醸成し、すべての復旦人の人々を働かせなければならない。そして調和のとれた雰囲気の中に住んでいます。</p>","id":"/home/swain/workspace/political-life/src/pages/ja/0143.md absPath of file >>> MarkdownRemark","fileAbsolutePath":"/home/swain/workspace/political-life/src/pages/ja/0143.md","frontmatter":{"title":"9月1日 木曜日"}}},"pathContext":{"id":"/home/swain/workspace/political-life/src/pages/ja/0143.md absPath of file >>> MarkdownRemark"}}

